import React from 'react'
import { GrTechnology } from 'react-icons/gr'

const Nav = () => {
  return (
    <div className='transition-all duration-200 h-[12vh] z-[100] fixed w-full'>
      <div className='flex items-center h-full justify-between w-[90%] xl:w-[80%] mx-auto '>
        {/* logo */}
        <div className='flex items-center space-x-2 '>
          <div className='w-10 h-10 rounded-full flex items-center justify-center bg-blue-800'>
              <GrTechnology className='w-6 h-6 text-white'/>
          </div>
          <h1 className='text-xl hidden sm:block font-bold md:text-2xl text-blue-600'>TechDev</h1>
        </div>
      </div>
      {/* NavLinks */}
      <div className='hidden lg:flex items-center space-x-10'>
        {nav}

      </div>
    </div>
  )
}

export default Nav
